.Header {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 10;
    background-color: $color-header-background;
    text-overflow: ellipsis;
    color: $color-header-content;
    border-bottom: 1px solid rgba($color-header-content, 0.1);
}

.Header-title {
    @include link-default(inherit, none);
    padding: 0.5rem 1rem;
    font-size: .875rem; // 14px
    line-height: (24 / 14);
    white-space: nowrap;
}

.Header-button {
    @include border-inline(end, 1px solid rgba($color-header-content, 0.1));
    display: block;
    border: 0;
    height: 2.5rem;
    width: 2.5rem;
    touch-action: manipulation;
    color: inherit;
    cursor: pointer;

    &:focus,
    &:active {
        outline: none;
    }

    &:hover {
        background-color: rgba($color-header-content, 0.05);
    }

    &:active {
        background-color: rgba($color-header-content, 0.1);
    }
}

.Header-navToggle {
    svg {
        display: none;
        margin: 0 auto;
        fill: rgba($color-header-content, 0.75);
        width: 1rem;
        height: 1rem;
    }
}

.Frame {
    .Header-navToggleIcon--open {
        display: block;
    }
}

.Frame.is-closed {
    .Header-navToggleIcon--open {
        display: none;
    }
    .Header-navToggleIcon--closed {
        display: block;
    }
}
