$font-family-monospace: SFMono-Regular, Consolas, "Liberation Mono", Menlo, Courier, monospace !default;
$font-family-sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default !default;

$color-link: #0747A6;
$color-text: #222 !default;
$color-heading: #222 !default;

$color-background: #fff !default;
$color-background-offset: #F5F5F5 !default;

$color-ui-hover: rgba($color-text, 0.05) !default;
$color-ui-active: rgba($color-text, 0.075) !default;

$color-header-background: #fff;
$color-header-content: #222;

$color-frame-border: rgba($color-text, 0.1) !default;

$color-sidebar-heading: rgba($color-text, 0.75) !default;

$highlight-theme: 'github' !default;
