.Tree {
    white-space: nowrap;
}

.Tree-title {
    @include font(label);
    padding: .5rem 1rem;
    color: $color-sidebar-heading;
    position: sticky;
    top: 0;
    background: rgba(255, 255, 255, 0.9);
    background: #fff;
    z-index: 1000;
    border-bottom: 1px solid rgba(34,34,34,0.1);
    box-shadow: 0 2px 0 0 rgba(0,0,0,0.02);
}

.Tree-collection {
    &.is-closed {
        background-color: transparent;

        .Tree-collectionItems {
            display: none;
        }

        .Tree-collectionLabel {
            span::before {
                [dir="ltr"] & {
                    background-image: url("../img/icon-navigation-closed-ltr.svg");
                }
                [dir="rtl"] & {
                    background-image: url("../img/icon-navigation-closed-rtl.svg");
                }
            }
        }
    }
}

.Tree-collectionLabel {
    @include font(caption);
    padding-top: 0.375rem;
    padding-bottom: 0.375rem;
    cursor: pointer;
    touch-action: manipulation;
    user-select: none;
    color: $color-text;
    margin-right: .75rem;
    border-radius: .1875rem;

    &:hover {
        background-color: $color-ui-hover;
    }

    span {
        display: block;
        position: relative;
        margin-left: 0.25rem;
    }

    span::before {
        position: relative;
        top: 1px;
        display: inline-block;
        width: 0.75rem;
        height: 0.75rem;
        background-image: url("../img/icon-navigation-opened.svg");
        background-size: 0.5rem;
        background-repeat: no-repeat;
        background-position: 0 50%;
        opacity: 0.5;
        content: "";
    }
}

.Tree-item {
    @include font(caption);
    margin-left: .5rem;
}

.Tree-entityLink {
    display: flex;
    align-items: center;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    padding-left: 0.75rem;
    text-decoration: none;
    margin-right: .75rem;
    border-radius: .1875rem;

    &:hover {
        background-color: $color-ui-hover;
    }

    .is-current & {
        background-color: $color-link;
        color: #fff;
    }

    .Status {
        @include margin-inline(end, 0.25rem);
        margin-left: -0.25rem;
    }
}

@for $i from 1 through 8 {
    .Tree-depth-#{$i} {
        $marLeft: ($i * .25rem);

        .Tree-collectionLabel,
        .Tree-entityLink {
            @include margin-inline(start, $marLeft);
        }
    }
}

.Tree-items {
    padding: .75rem 0;
}